.lumens-contain {
  h1 {
    text-align: left;
    padding-left: 25px;
    font-size: 18px;
    line-height: 24px;
  }
  border: 1px solid #f1f1f5;

  box-shadow: 0px 12px 18px rgba(23, 23, 37, 0.04);
  border-radius: 4px;
}
.lumens-details {
  display: flex;
  padding: 0 25px 25px 25px;
  overflow-x: scroll;
  .lumens-items {
    width: 200px;
    padding-right: 20px;
    p {
      text-align: left;
    }
    .value {
      color: #0062ff;
      font-size: 16px;
      line-height: 24px;
      font-weight: 600;
    }
    .text {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #92929d;
      padding: 0px 20px 0px 15px;
    }
  }
}
